import React, { useState } from 'react'
import Todo from './Todo'

export default function Todos() {
  const styles = {
    div: {
      padding: '20px',
      display: 'flex',
      margin: '0 auto',
      width: '600px',
      minHeight: '300px',
      height: 'auto',
      border: '1px solid black',
      borderRadius: '15px',
      marginTop: '20px',
      justifyContent: 'center',
      alignItems: 'center',
      flexDirection: 'column',
    },
    divAdd: {
      display: 'flex',
      margin: '15px auto 0',
      width: '600px',
      justifyContent: 'center',
    },
  }
  const [todos, setTodos] = useState(JSON.parse(localStorage.getItem('todos')))
  const [inputTitle, setInputTitle] = useState('')
  const [duration, setDuration] = useState('')

  function findMaxId(arr) {
    let max = arr[0].id
    for (let i = 1; i < arr.length; i++) {
      if (arr[i].id > max) max = arr[i].id
    }
    return max
  }
  function changeDone2(id) {
    todos.forEach((item) => {
      if (item.id === id) item.done++
    })
    setLocalStorage(todos)
  }
  function removeTodo(id) {
    setTodos(todos.filter((item) => item.id !== id))

    todos.forEach((item, index) => {
      if (index >= id) item.id--
    })
  }
  async function addTodo() {
    if (!todos || todos.length === 0) {
      setTodos([{ id: 1, title: inputTitle, duration: duration, done: 0 }])
    } else {
      let id = findMaxId(todos) + 1
      setTodos(
        todos.concat([
          { id: id, title: inputTitle, duration: duration, done: 0 },
        ])
      )
    }

    setInputTitle('')
    setDuration('')
  }
  function setLocalStorage(todos) {
    localStorage.setItem('todos', JSON.stringify(todos))
    console.log(JSON.parse(localStorage.getItem('todos')))
  }
  if (todos) {
    return (
      <div>
        <div style={styles.div}>
          {todos.map((todo) => (
            <Todo
              key={todo.id}
              changeDone2={changeDone2}
              updateData={removeTodo}
              value={todo}
            />
          ))}
        </div>
        <div style={styles.divAdd}>
          <input
            value={inputTitle}
            type='text'
            minLength='1'
            onChange={(e) => setInputTitle(e.target.value)}
          />
          <input
            type='number'
            value={duration}
            minLength='1'
            onChange={(e) => setDuration(e.target.value)}
          />
          <button onClick={addTodo}>Добавить задачу</button>
        </div>
        <button onClick={() => setLocalStorage(todos)}>Сохранить</button>
        <button onClick={() => localStorage.clear()}>Удалить все</button>
      </div>
    )
  } else
    return (
      <div>
        <div style={styles.divAdd}>
          <input
            value={inputTitle}
            type='text'
            minLength='1'
            onChange={(e) => setInputTitle(e.target.value)}
          />
          <input
            type='number'
            value={duration}
            minLength='1'
            onChange={(e) => setDuration(e.target.value)}
          />
          <button onClick={addTodo}>Добавить задачу</button>
        </div>
      </div>
    )
}
