import React, { useState } from 'react'
import Modal from './Modal'

export default function Todo(props) {
  const styles = {
    div: {
      display: 'flex',
      marginTop: '10px',
      cursor: 'pointer',
    },
  }
  const [isVisible, setVisibility] = useState(false)
  function changeVis() {
    setVisibility(!isVisible)
  }
  function changeDone(id) {
    props.changeDone2(id)
  }
  return (
    <div style={styles.div}>
      <div
        onClick={() => {
          setVisibility(!isVisible)
        }}>
        {props.value.id}) {props.value.title} ({props.value.duration} дней)
        (осталось {props.value.duration - props.value.done})
      </div>
      <button
        onClick={() => {
          props.updateData(props.value.id)
        }}>
        Удалить
      </button>
      <Modal
        changeVis={changeVis}
        changeDone={changeDone}
        value={props.value}
        isVisible={isVisible}
      />
    </div>
  )
}
