import React from 'react'
import './index.css'

export default function Modal(props) {
  const styles = {
    input: {
      width: '25px',
      height: '25px',
      margin: '10px',
    },
    checkbox: {},
    back: {
      textAlign: 'center',
      padding: '20px 0',
    },
  }

  const title = props.value.title
  const duration = props.value.duration
  const done = props.value.done

  let item = []
  function p(e) {
    e.preventDefault()
    return false
  }
  for (let i = 0; i < done; i++) {
    item.push(
      <input type='checkbox' checked onClick={p} style={styles.input} />
    )
  }
  item.push(
    <input
      type='checkbox'
      onClick={() => {
        props.changeDone(props.value.id)
      }}
      style={styles.input}
    />
  )
  for (let i = 1; i < duration - done; i++) {
    item.push(<input type='checkbox' onClick={p} style={styles.input} />)
  }
  if (props.isVisible)
    return (
      <div className='modal'>
        <div onClick={props.changeVis} style={styles.back}>
          {title}
        </div>
        <div style={styles.inputs}>{item}</div>
      </div>
    )
  else return ''
}
